import React, { useContext, useState } from "react";
import ReactDOM from "react-dom";
import ChangeWordColor from "./ChangeWordColor";

interface ICreator {
  userId: number;
  nickname: string;
}

interface ISearchPlaylist {
  name: string;
  id: number;
  durationms: number;
  //   accountId?: bigint;
  //   alias: string[];
  coverImgUrl: string;
  trackCount: number;
  playCount: number;
  bookCount: number;
  creator: ICreator;
}

interface ISearchPlaylistsProps {
  value: string;
  type: number; //type:  1  ,
  // : 1: , 10: , 100: , 1000: , 1002: , 1004: MV, 1006: , 1009: , 1014: , 1018:
  playlistCount: number;
  playlists: Array<ISearchPlaylist>;
}

function SearchPlaylists(props: ISearchPlaylistsProps) {
  return (
    <div className="search-res-playlists">
      <ul>
        {props.playlists.map((playlist, index) => {
          return (
            <li className="search-res-playlist" key={index}>
              <img src={playlist.coverImgUrl}></img>
              <p>
                <a>
                  <ChangeWordColor str={playlist.name} word={props.value} />
                </a>
              </p>
              <span>
                <span className="iconfont" title="">
                  &#xe664;
                </span>
                <span className="iconfont" title="">
                  &#xe656;
                </span>
                <span className="iconfont" title="">
                  &#xe764;
                </span>
              </span>
              <p>{playlist.trackCount}</p>
              <p>
                by&nbsp;&nbsp;
                <a>{playlist.creator ? playlist.creator.nickname : ""}</a>
              </p>
              <p>
                :&nbsp;&nbsp;
                {playlist.bookCount > 100000
                  ? Math.floor(playlist.bookCount / 10000) + ""
                  : playlist.bookCount + ""}
              </p>
              <p>
                :&nbsp;&nbsp;
                {playlist.playCount > 100000
                  ? Math.floor(playlist.playCount / 10000) + ""
                  : playlist.playCount + ""}
              </p>
            </li>
          );
        })}
      </ul>
    </div>
  );
}

export default SearchPlaylists;
