import axios, { AxiosResponse } from "axios";
import { type } from "os";
import React, { useContext, useState } from "react";
import ReactDOM from "react-dom";

interface ISearchResProps {
  keywords: string;
  limit?: number;
  offset?: number;
  type?: number;
  //limit :  ,  30
  //offset :  ,  :(  -1)*30,  30  limit  ,  0
  //type:  1  ,
  // : 1: , 10: , 100: , 1000: , 1002: , 1004: MV, 1006: , 1009: , 1014: , 1018:
}

interface IArtist {
  id: number;
  name: string;
}

interface IAlbum {
  id: number;
  name: string;
}

interface IRange {
  first: number;
  second: number;
}

interface ILyrics {
  txt: string;
  range: IRange[];
}

interface ISearchSong {
  id: number;
  name: string;
  artists: IArtist[];
  album: IAlbum;
  duration: number;
  transNames: string[];
  lyrics: ILyrics;
}

interface ISearchLyricsProps {
  type: number; //type:  1  ,
  // : 1: , 10: , 100: , 1000: , 1002: , 1004: MV, 1006: , 1009: , 1014: , 1018:
  songCount: number;
  songs: Array<ISearchSong>;
}

interface IResponseResultLyric {
  code: number;
  result: ISearchLyricsProps;
}

interface ISearchProps {
  host: string; // http://localhost:3001
  url: string; //  /search
  param: ISearchResProps;
}

async function getSearchDataLyric(
  props: ISearchProps
): Promise<IResponseResultLyric> {
  const data = await axios({
    method: "get",
    url: props.host + props.url,
    params: {
      keywords: props.param.keywords,
      type: props.param.type,
      limit: props.param.limit,
      offset: props.param.offset,
    },
  });
  //   console.log(data.data);

  return data.data;
}

export default getSearchDataLyric;
export type { ISearchLyricsProps };
