import axios, { AxiosResponse } from "axios";
import { type } from "os";
import React, { useContext, useState } from "react";
import ReactDOM from "react-dom";

interface ISearchResProps {
  keywords: string;
  limit?: number;
  offset?: number;
  type?: number;
  //limit :  ,  30
  //offset :  ,  :(  -1)*30,  30  limit  ,  0
  //type:  1  ,
  // : 1: , 10: , 100: , 1000: , 1002: , 1004: MV, 1006: , 1009: , 1014: , 1018:
}
//start

interface ISearchUser {
  nickname: string;
  signature: string;
  userId: number;
  avatarUrl: string;
  userType: number;
  gender: number;
}

interface ISearchUsersProps {
  type: number; //type:  1  ,
  // : 1: , 10: , 100: , 1000: , 1002: , 1004: MV, 1006: , 1009: , 1014: , 1018:
  userprofileCount: number;
  userprofiles: Array<ISearchUser>;
}

interface IResponseResultUser {
  code: number;
  result: ISearchUsersProps;
}
//end

interface ISearchProps {
  host: string; // http://localhost:3001
  url: string; //  /search
  param: ISearchResProps;
}

async function getSearchDataUser(
  props: ISearchProps
): Promise<IResponseResultUser> {
  const data = await axios({
    method: "get",
    url: props.host + props.url,
    params: {
      keywords: props.param.keywords,
      type: props.param.type,
      limit: props.param.limit,
      offset: props.param.offset,
    },
  });
  return data.data;
}

export default getSearchDataUser;
export type { ISearchUsersProps };
